<template>
	<view class="content">
		<uni-collapse :accordion="true">
			<uni-collapse-item title="租赁周期">
				<view class="item-content">
					平台提供分钟租、时租、周租、日租、月租、季租、年租以及以租代购的服务，租赁订单即时生效，请在租约到期前完成续租或退租操作
				</view>
			</uni-collapse-item>
			<uni-collapse-item title="租赁有什么条件">
				<view class="item-content">
					只要芝麻信用分达到650分（不同车型可能不同）即可享受免押金服务，如果信用分达不到或平台设置了收取押金，就需要支付押金和租赁费用才可进行租赁
				</view>
			</uni-collapse-item>
			<uni-collapse-item title="续租怎么操作">
				<view class="item-content">
					在您的租赁界面中点击续租操作，支付续租费用后将在原来的订单中增加租赁时限；如果超期后续租的，续租的时长没有达到超期的时长，租赁状态还是处于超期状态
				</view>
			</uni-collapse-item>
			<uni-collapse-item title="以租代购怎么操作">
				<view class="item-content">
					在租车套餐中选择以租代购套餐，在租赁周期内连续、按月租赁，到达租赁周期条件后即可购得车辆，同时平台不再对外出租该车辆；支持按月续租
				</view>
			</uni-collapse-item>
			<uni-collapse-item title="换车/电">
				<view class="item-content">
					租赁后可以点击租赁界面上的换车/电操作，换车/电只可以换同种类目的租赁物品，而且超期的租赁物品不能进行换车/电，需要先进行续租
				</view>
			</uni-collapse-item>

			<uni-collapse-item title="还车/电">
				<view class="item-content">
					还车/电时，如果不在门店附近会还不了，需要到门店后才能还；如果还车/电人工审核的，需由门店人员检验后，在管理平台上点击还车/电；请保持租赁物品干净整洁
				</view>
			</uni-collapse-item>
			<uni-collapse-item title="逾期有什么样的风险，超期后还能用吗">
				<view class="item-content">
					平台提供的芝麻信用免押金服务基于您的征信分值，若有违约行为，将给您的征信记录带来不良影响；如果租赁物品超期后，系统将锁定您的租赁物品，租赁物品无法骑行（续租之后方可恢复），未免给您带来不必要的影响，请务必在租约结束前还车或续租
				</view>
			</uni-collapse-item>
			<uni-collapse-item title="支付方式">
				<view class="item-content">
					租赁支付方式有线上支付和线下支付，线上支付只需到门店在线上支付租金和押金就可进行租赁了，线下支付不可享受芝麻信用免押金服务，需要到门店把租金和押金交给门店人员，由门店人员下发租赁启用后，才可进行租赁
				</view>
			</uni-collapse-item>
			<uni-collapse-item title="提前退租">
				<view class="item-content">
					如果您提前退租，不满租赁周期按租赁周期计算，已收取的租金不予退还
				</view>
			</uni-collapse-item>
		</uni-collapse>

		<view class="bottom-wrapper">

			<button :style="{'color':globalData.mainColor}" size="default" hover-class="none" @click="contactService()">联系客服</button>
			<button :style="{'color':globalData.mainColor}" size="default" hover-class="none" @click="toFaultDeclare()">故障申报</button>

		</view>
	</view>
</template>

<script>
	import {
		request_get
	} from '../../utils/http.js';
	import {
		goPage
	} from '../../utils/util.js';
	const app = getApp();
	export default {
		data() {
			return {
				globalData: app.globalData,
				bicycleNumber: '',
				servicePhone: ''
			}
		},
		onLoad(options) {
			this.getServicePhone();
			var vehicleNo = typeof options.vehicleNo != "undefined" ? options.vehicleNo : '';
			this.bicycleNumber = vehicleNo;
		},
		methods: {
			contactService() {
				uni.makePhoneCall({
					phoneNumber: this.data.servicePhone ? this.data.servicePhone : app.globalData.servicePhone
				});
			},

			// 获取客服电话
			getServicePhone() {
				let url = app.globalData.baseUrl + '/customer/svc/phone'
				request_get(url, (res) => {
					if (res.data.ret) {
						this.servicePhone = res.data.data;
					}
				})
			},

			toFaultDeclare() {
				let machineNo = '';
				if (this.bicycleNumber != '') {
					machineNo = this.bicycleNumber;
				}
				goPage('/pages/fault-declare/fault-declare', {
					vehicleNo: machineNo
				});
			},
		}
	}
</script>

<style>
	.content {
		position: relative;
	}

	.collapse-wrapper {
		padding-bottom: 160rpx;
	}

	.item-content {
		padding: 28rpx 32rpx;
		font-size: 34rpx;
		color: #333;
		line-height: 48rpx;
		background-color: #F5F5F5;
	}

	.bottom-wrapper {
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
	}

	.bottom-wrapper>button {
		display: inline-block;
		width: 50%;
		border-bottom: none;
	}

	.uni-collapse-cell__title {
		background-color: #fff;
	}
</style>
